import React from "react";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import pic3 from "./サイレントマジョリティー.png"

const Home = () =>{
  return <div>aiueo</div>;
}

const Photo = () =>{
  return <div>graphy</div>
}

const Body =() =>{
  return (
    <Router>
      <div>
        <Link to="/">Home</Link>
        <Link to="/photo">Photo</Link>
      </div>
      <Switch>
        <Route path="/photo">
          <Photo />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  )
}

const Head = () =>{
    return(
      <section class="hero is-primary is-medium">
      <div class="hero-body">
        <div class="container">
          <h1 class="title">
            らふみみこぴ
          </h1>
          <h2 class="subtitle">
            piano
          </h2>
        </div>
      </div>
    </section>
    );
}


const Tabs = () =>{
  return(
    <div class="tabs is-medium">
  <ul>
    <li class="is-active"><a>らふみみこぴ</a></li>
    <li><a>楽譜一覧</a></li>
    <li><a>ギャラリー</a></li>
    <li><a></a></li>
  </ul>
</div>
  );
}


const Bod = () =>{
  return(
    <body>
  <section class="section">
    <div class="container">
      <h1 class="title">耳コピ（ピアノ）</h1>
      <h2 class="subtitle">
        みなさん、耳コピ（耳コピー）をしたことはありますか？耳コピとは耳できいた音楽を
        楽器などを使って再現することです。特にピアノでの耳コピは右手でメロディー、左手で
        伴奏を弾くことができるので再現率が高くより楽しむことができます。
        よくYoutubeやSNSに動画をあげているのをよく
        見ると思います。どれも完成度が高く、素晴らしいものばかりです。
      </h2>
      <h1 class="title"></h1>
      <h2 class="subtitle">
        
      </h2>
    </div>
  </section>
</body>
  )
}



const Bottom = () =>{
    return(
        <section className="hero is-primary is-light">
  <div className="hero-body">
    <div className="container">
      &copy;minori
    </div>
  </div>
</section>
    )
}


const App = () =>{
    return (
        <div>
            <Head />
            <Body />
        </div>
    );
};

export default App;